import React from "react";
import { Link } from "react-router-dom";
import { createStructuredSelector } from "reselect";

import { ReactComponent as Logo } from "../../assets/crown.svg";
import CartIcon from "../cart-icon/cart-icon.component";

import { connect } from "react-redux";
import { signOut } from "../../redux/auth/authActions";
import CartDropdown from "../cart-dropdown/cart-dropdown.component";

import { CurrentUserLogin } from "../../redux/user/user.selector";
import { cartHidden } from "../../redux/cart/cart.selector";

import "./header.styles.scss";

const Header = ({ isAuthenticated, showCart, signOut }) => (
  <div className='header'>
    <Link className='logo-container' to='/'>
      <Logo className='logo' />
    </Link>
    <div className='options'>
      <Link className='option' to='/shop'>
        SHOP
      </Link>
      <Link className='option' to='/shop'>
        CONTACT
      </Link>
      {isAuthenticated ? (
        <Link className='option' to='/signin' onClick={signOut}>
          SIGNOUT
        </Link>
      ) : (
        <Link className='option' to='/signin'>
          SIGNIN
        </Link>
      )}
      <CartIcon />
    </div>
    {showCart ? <CartDropdown /> : null}
  </div>
);

const mapStateToProps = createStructuredSelector({
  isAuthenticated: CurrentUserLogin,
  showCart: cartHidden,
});

export default connect(mapStateToProps, { signOut })(Header);
